import { NgModule } from '@angular/core';
import { BrowserModule } from '@angular/platform-browser';
import { Routes, RouterModule } from '@angular/router';

// Import Components
import { AppComponent } from './components/app/component';
import { HomeComponent } from './components/homepage/component';
import { HeaderComponent } from './components/header/component';
import { NotFoundComponent } from './components/not_found/component';
import { UnderConstructionComponent } from './components/under_construction/component';

const routes: Routes = [
    {
        path: '',
        redirectTo: '/home',
        pathMatch: 'full'
    },
    {
        path: 'home',
        component: HomeComponent
    },
    {
        path: 'skills',
        component: UnderConstructionComponent
    },
    {
        path: 'timeline',
        component: UnderConstructionComponent
    },
    {
        path: 'about',
        component: UnderConstructionComponent
    },
    {
        path: '**',
        component: NotFoundComponent
    }
];

@NgModule({
  imports: [
    BrowserModule,
    RouterModule.forRoot(routes, { useHash: true })
  ],
  declarations: [
    AppComponent,
    HomeComponent,
    HeaderComponent,
    NotFoundComponent,
    UnderConstructionComponent
  ],
  bootstrap: [ AppComponent ]
})
export class AppModule {
    isMobile: boolean;
    constructor() {
        let mobileRegex = /(mobile)/i;
        let isMobile = mobileRegex.test(navigator.userAgent);

        if(isMobile) {
            document.getElementsByTagName('body')[0].classList.add('mobile-device');
        }
    }
}



